import { Component } from '@angular/core';
import { NavController, ToastController, AlertController, LoadingController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../app/app.config';
import 'rxjs/add/operator/map';

@Component({
    selector: 'page-home',
    templateUrl: 'home.html'
})
export class HomePage {
    djycookie = localStorage.djycookie;
    xfwcookie = localStorage.xfwcookie;
    bannerurl = AppConfig.getUrl() + "/banner/page?pageSize=10&pageNumber=0"
    bannerarr: any;
    len:any = 0;

    constructor(public navCtrl: NavController, public toastCtrl: ToastController, public http: Http, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {
        this.banner()
    }
    banner() {
        this.bannerarr = [];
        let loading = this.loadingCtrl.create({
            content: '加载中...'
        });
        loading.present();
        return new Promise((resolve, reject) => {
            this.http.get(this.bannerurl)
                .map(res => res.json())
                .subscribe(data => {
                    resolve(data);
                    if (data.status) {
                        for (let i = 0; i < data.data.length; i++) {
                            this.bannerarr.push({
                                name: data.data[i].name,
                                imgs: data.data[i].imgs[0],
                                descs: data.data[i].descs,
                            })
                        }
                        console.log(this.bannerarr)
                        this.len = this.bannerarr.length;
                    } else {
                        let alert = this.alertCtrl.create({
                            title: data.msg
                        });
                        alert.present();
                    }
                }, err => {
                    let alert = this.alertCtrl.create({
                        title: err
                    });
                    alert.present();
                })
            loading.dismiss();
        })
    }
    goto(id) {
        if (!localStorage.djycookie || !localStorage.xfwcookie) {
            const toast = this.toastCtrl.create({
                message: '需要先绑定党建云和先锋网账号',
                duration: 3000,
                position: 'top'
            });
            toast.present();
            this.navCtrl.push("BindaddPage")
        } else {
            switch (id) {
                case 1:
                    this.navCtrl.push("TaskPage");
                    break;
                case 2:
                    this.navCtrl.push("StudyPage");
                    break;
                case 3:
                    this.navCtrl.push("OrganizePage")
                    break;
                case 4:
                    this.navCtrl.push("WorkPage");
                    break;
                case 5:
                    this.navCtrl.push("MessagePage");
                    break;
                case 6:
                    this.navCtrl.push("ActivitylistPage")
                    break;
                case 7:
                    this.navCtrl.push("DutyPage")
                    break;
            }
        }
    }
    article() {
        this.navCtrl.push("AriclePage")
    }
}



