import { Component } from '@angular/core';
import { NavController, AlertController, LoadingController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../app/app.config';
import 'rxjs/add/operator/map';
@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
  about = "1";
  url = AppConfig.getUrl() + "/notification/page?pageSize=10&userId=" + localStorage.userid
  arr: any;
  pageNo = 0;
  constructor(public navCtrl: NavController, public http: Http, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {

    this.get()
  }

  get(event?) {
    if (event) {
      this.pageNo++;
    } else {
      this.pageNo = 0;
      this.arr = [];
    }
    let loading = this.loadingCtrl.create({
      content: '加载中...'
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.http.get(this.url + "&type=" + this.about + "&pageNumber=" + this.pageNo)
        .map(res => res.json())
        .subscribe(data => {
          resolve(data);
          if (event)
            event.complete();
          if (data.status) {
            for (let i = 0; i < data.data.length; i++) {
              this.arr.push({
                id: i + (10 * this.pageNo),
                tid: data.data[i].id,
                title: data.data[i].title,
                attachments: data.data[i].attachments[0],
                notificationTitle: data.data[i].notificationTitle,
                descs: data.data[i].descs,
                createdAt: new Date(data.data[i].createdAt).getFullYear() + "-" + (new Date(data.data[i].createdAt).getMonth() * 1 + 1) + "-" + new Date(data.data[i].createdAt).getDate(),
              })
            }
            console.log(this.arr)
          } else {
            let alert = this.alertCtrl.create({
              title: data.msg
            });
            alert.present();
          }
        }, err => {
          let alert = this.alertCtrl.create({
            title: err
          });
          alert.present();
        })
      loading.dismiss();
    })
  }
  goto(id) {
    this.navCtrl.push("AboutmorePage", { article: this.arr[id] })
  }
}



